import { Component, Input, Output, EventEmitter, DynamicComponentLoader, QueryList, Query, ElementRef, AfterViewInit } from 'angular2/core';
import { TreeNode } from '../models/tree-node.model';
import { TreeModel } from '../models/tree.model';

@Component({
  selector: 'TreeNode',
  directives: [TreeNodeComponent],
  styles: [
    '.tree-children { padding-left: 20px }',
    `.node-content-wrapper {
      display: inline-block;
      padding: 2px 5px;
      border-radius: 2px;
      transition: background-color .15s,box-shadow .15s;
    }`,
    '.tree-node-active > .node-content-wrapper { background: #beebff }',
    '.tree-node-active.tree-node-focused > .node-content-wrapper { background: #beebff }',
    '.tree-node-focused > .node-content-wrapper { background: #e7f4f9 }',
    '.node-content-wrapper:hover { background: #f7fbff }',
    '.tree-node-active > .node-content-wrapper, .tree-node-focused > .node-content-wrapper, .node-content-wrapper:hover { box-shadow: inset 0 0 1px #999; }',

    '.tree-node-expanded > .toggle-children { transform: rotate(90deg) }',
    '.tree-node-collapsed > .toggle-children { transform: rotate(0); }',
    `.toggle-children {
        background-image: url(\'data:image/png;base64,\');
        height: 8px;
        width: 9px;
        background-size: contain;
        display: inline-block;
        position: relative;
        background-repeat: no-repeat;
        background-position: center;        
    }`,
    `.toggle-children-placeholder {
        display: inline-block;
        height: 10px;
        width: 10px;
        position: relative;
        top: 1px;
    }`
  ],
  template: `
    <div class="tree-node tree-node-level-{{ node.level }}"
      [class.tree-node-expanded]="node.isExpanded && node.hasChildren"
      [class.tree-node-collapsed]="node.isCollapsed && node.hasChildren"
      [class.tree-node-leaf]="node.isLeaf"
      [class.tree-node-active]="node.isActive"
      [class.tree-node-focused]="node.isFocused">
      <span
        *ngIf="node.hasChildren"
        class="toggle-children"
        (click)="node.toggle()">
      </span>
      <span
        *ngIf="!node.hasChildren"
        class="toggle-children-placeholder">
      </span>
      <div class="node-content-wrapper" (click)="node.toggleActivated()">
        <span #treeNodeContent></span>
      </div>
      <div class="tree-children" [hidden]="node.isCollapsed">
        <TreeNode
          *ngFor="#node of node.children"
          [node]="node">
        </TreeNode>
      </div>
    </div>
  `
})

export class TreeNodeComponent implements AfterViewInit {
  @Input() node:TreeNode;

  constructor(private componentLoader: DynamicComponentLoader,
              private elementRef: ElementRef,
              private treeModel:TreeModel) {
  }

  ngAfterViewInit() {
      this._loadTreeNodeContent();
  }

  ngOnChanges() {
    this.node.elementRef = this.elementRef;
  }

  _loadTreeNodeContent() {
    this.componentLoader.loadIntoLocation(this.treeModel.treeNodeContentComponent,
                                          this.elementRef,
                                          'treeNodeContent')
      .then((componentRef) => {
        componentRef.instance.node = this.node;
      });
  }
}



